.icon-inner {
  display: none;
}
@media (max-width: 47.99875em) {
  .icon-inner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.1875rem;
    height: 1.875rem;
    border: 0.125rem solid #2e4d7e;
    border-radius: 0.1875rem;
    transition: 0.3s ease-in-out;
    z-index: 7;
  }
}

.icon-menu {
  display: none;
}
@media (max-width: 47.99875em) {
  .icon-menu {
    position: relative;
    display: block;
    flex: 0 0 1.875rem;
    width: 1.875rem;
    height: 1.125rem;
    cursor: pointer;
    z-index: 7;
  }
  .icon-menu span {
    transition: all 0.3s ease 0s;
    top: 0.5rem;
    right: 0;
    position: absolute;
    width: 100%;
    height: 0.125rem;
    background-color: #2e4d7e;
  }
  .icon-menu span:nth-child(1) {
    top: 0;
  }
  .icon-menu span:nth-child(2) {
    width: 75%;
  }
  .icon-menu span:nth-child(3) {
    top: auto;
    bottom: 0;
    width: 50%;
  }
  .menu-open .icon-menu span {
    background-color: #2e4d7e;
  }
  .menu-open .icon-menu span {
    transform: scale(0);
  }
  .menu-open .icon-menu span:first-child {
    transform: rotate(-45deg);
    top: 0.5rem;
  }
  .menu-open .icon-menu span:last-child {
    transform: rotate(45deg);
    bottom: 0.5rem;
    width: 100%;
  }
}

@media (max-width: 47.99875em) {
  .menu {
    text-align: center;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding-top: 7.5rem;
    overflow: auto;
    background: rgba(0, 0, 0, 0.7);
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;
    z-index: 4;
  }
  .menu-open .menu {
    opacity: 1;
    visibility: visible;
  }
  .menu-open .menu::after {
    background: rgba(192, 192, 192, 0.9);
  }
  .menu:after {
    content: "";
    position: fixed;
    background: rgba(192, 192, 192, 0);
    top: 0;
    left: 0;
    width: 100%;
    height: 4.375rem;
    transition: 0.3s ease-in-out;
    z-index: 5;
  }
}

.marg, .form {
  margin-bottom: 0.9375rem;
}
@media (max-width: 43.75em) {
  .marg, .form {
    margin-bottom: calc(0.625rem + 5 * ((100vw - 20rem) / 380));
  }
}

.row {
  display: flex;
  align-items: center;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 0.9375rem;
}

.gap {
  display: flex;
  align-items: center;
  gap: 0.9375rem;
}
@media (max-width: 43.75em) {
  .gap {
    gap: calc(0.3125rem + 10 * ((100vw - 20rem) / 380));
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 2.1875rem;
  width: 100%;
  font-size: 1.875rem;
  font-weight: 500;
  color: #528be6;
  border: 0.0625rem solid #528be6;
  border-radius: 1.875rem;
  padding: 0rem 0.625rem;
  background-color: rgb(61, 60, 73);
  transition: 0.3s ease-in-out;
}
@media (max-width: 43.75em) {
  .btn {
    font-size: calc(1.6875rem + 3 * ((100vw - 20rem) / 380));
  }
}
@media (any-hover: hover) {
  .btn:hover {
    color: rgb(61, 204, 14);
    border: 0.0625rem solid rgb(61, 204, 14);
    background-color: rgb(43, 42, 51);
  }
}
.btn:focus {
  box-shadow: 0rem 0rem 0.9375rem rgb(31, 243, 27);
  background-color: rgb(43, 42, 51);
  color: #ffa500;
}
.btn span {
  font-size: 1.5rem;
}
@media (max-width: 43.75em) {
  .btn span {
    font-size: calc(1.25rem + 4 * ((100vw - 20rem) / 380));
  }
}

.input {
  min-height: 1.875rem;
  padding: 0 0.625rem;
  border: 0.0625rem solid orange;
  transition: 0.3s ease-in-out;
}
.input::-moz-placeholder {
  font-size: 0.875rem;
