  font-style: italic;
}
.input::placeholder {
  font-size: 0.875rem;
  font-weight: 300;
  font-style: italic;
}
.input:focus {
  box-shadow: 0rem 0rem 0.9375rem rgb(31, 243, 27);
}

.img {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.descr {
