  transition: 0.3s ease-in-out;
}
@media (max-width: 43.75em) {
  .descr {
    font-size: calc(0.875rem + 2 * ((100vw - 20rem) / 380));
  }
}

.title {
  text-align: center;
  font-size: 1.875rem;
  font-weight: 500;
  margin-bottom: 0.4166666667em;
}
@media (max-width: 43.75em) {
  .title {
    font-size: calc(1.5rem + 6 * ((100vw - 20rem) / 380));
  }
}
.title span {
  color: orange;
  background: linear-gradient(45deg, #ffa500, #ff4500, #08f408, #f70a59, #ff00ff, #0000ff, #1d6cea, #00ffff, #008000, #ff002b, #1e7cd9);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subtitle {
  font-size: 1.5rem;
  font-weight: 500;
  font-style: italic;
  color: #528be6;
