  transition: 0.3s ease-in-out;
}
@media (max-width: 43.75em) {
  .subtitle {
    font-size: calc(1.125rem + 6 * ((100vw - 20rem) / 380));
  }
}

.after {
  position: relative;
  padding-bottom: 0.1875rem;
}
@media (any-hover: hover) {
  .after:hover::after {
    width: 100%;
    left: 0;
  }
}
.after::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  height: 0.125rem;
  width: 0;
  background-color: rgba(82, 139, 230, 0.95);
  transition: 0.3s ease-in-out;
}

.after-img {
  position: relative;
}
@media (any-hover: hover) {
  .after-img:hover::after {
    opacity: 0;
  }
}
.after-img::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 167, 253, 0.3);
  transition: 0.3s ease-in-out;
}

.error {
  color: orangered;
  font-weight: 900;
  text-transform: uppercase;
}

.loader {
  display: flex;
  height: 100vh;
  background: linear-gradient(45deg, #78dbe2, #2783c4, #9627c4, #e04442, #e16623);
}
.loader__container {
  flex-grow: 1;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.circle {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 7.5rem;
  height: 7.5rem;
  border-radius: 50%;
  font-size: 1.125rem;
  font-weight: 700;
  color: #528be6;
  letter-spacing: 0.09375rem;
  text-transform: uppercase;
  border: 0.0625rem solid rgba(85, 139, 225, 0.3725490196);
}
.circle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0.1875rem solid transparent;
  border-top: 0.1875rem solid #528be6;
  border-left: 0.1875rem solid #528be6;
  border-bottom: 0.1875rem solid #528be6;
  border-radius: 50%;
  -webkit-animation: animateCircle 1.5s linear infinite;
          animation: animateCircle 1.5s linear infinite;
}
.circle span {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 0.0625rem;
  background: transparent;
  transform-origin: left;
  -webkit-animation: animate 1.5s linear infinite;
          animation: animate 1.5s linear infinite;
}
.circle span::before {
  content: "";
  position: absolute;
  top: -0.3125rem;
  right: -0.3125rem;
  width: 0.75rem;
  height: 0.75rem;
  background: #528be6;
  border-radius: 50%;
  box-shadow: 0rem 0rem 0.3125rem #528be6;
}

@-webkit-keyframes animate {
  0% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(405deg);
  }
}

@keyframes animate {
  0% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(405deg);
  }
}
@-webkit-keyframes animateCircle {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes animateCircle {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
